.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  text-align: left;

  @include onTablet() {
    font-size: 40px;
    margin-bottom: 56px;
  }

  @include onDesktop() {
    font-size: 48px;
  }

  &--align--left {
    text-align: left;
  }
}
